<template>
  <article class="verifyModules d-flex align-items-center">
    <span :class="['text-nowrap', {'text-danger': calcVerifyCode && verifyCode <= 0}]">
      <i v-if="calcVerifyCode" :class="['fa', verifyCode >= 1 ? 'fa-check-circle text-success' : 'fa-times-circle text-danger']" />
      驗證碼：
    </span>
    <Verify ref="Verify"
      :type="type"
      :codeLength="codeLength"
      :fontSize="fontSize"
      :width="width"
      :height="height"
      :showButton="showButton"
      @success="onVerifySuccess"
      @error="onVerifyError" />
  </article>
</template>

<script>
import Verify from 'vue2-verify'
export default {
  name: 'verifyCode',
  components: {
    Verify
  },
  data () {
    const { type, codeLength, fontSize, width, height, showButton } = this.$store.state.pageData.verifySetup
    return {
      type: type,
      codeLength: codeLength,
      fontSize: fontSize,
      width: width,
      height: height,
      showButton: showButton
    }
  },
  props: ['verifyCode'],
  computed: {
    calcVerifyCode () {
      return this.verifyCode !== null
    }
  },
  methods: {
    // 驗證碼正確
    onVerifySuccess () {
      this.$emit('updateVerifyCode', 1)
    },
    // 驗證碼錯誤
    onVerifyError (obj) {
      this.$emit('updateVerifyCode', 0)
      obj.refresh()
    }
  }
}
</script>
